import { PureComponent, ReactElement } from 'react';
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from 'reactstrap';
import logoPancakewap from '../../../asset/logo/pancakeswap.png';
import logoApewap from '../../../asset/logo/apeswap.png';
import style from './SelectDApp.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  // symbol: string;
  // address: string;
  // logoURI: string;
  // onClick: () => void;
}
interface IOwnState {
  isOpen: boolean;
}

class SelectDApp extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      isOpen: false,
    };

    this.onToggle = this.onToggle.bind(this);
  }

  onToggle() {
    this.setState((state: IOwnState) => ({
      isOpen: !state.isOpen,
    }));
  }

  render(): ReactElement {
    const {} = this.props;
    const { isOpen } = this.state;

    return (
      <Dropdown isOpen={isOpen} toggle={this.onToggle}>
        <DropdownToggle
          className={
            'd-flex justify-content-center align-items-center px-0 shadow-none ' +
            style.dropdownToggle
          }
        >
          <img className={style.logo} src={logoPancakewap} alt='' />
          <span className={style.title}>PancakeSwap</span>
        </DropdownToggle>
        <DropdownMenu className={style.dropdownMenu}>
          <DropdownItem disabled>
            <img className={style.logo} src={logoPancakewap} alt='' />
            <span className={style.title}>PancakeSwap</span>
          </DropdownItem>
          <DropdownItem>
            <img className={style.logo} src={logoApewap} alt='' />
            <span className={style.title}>ApeSwap</span>
          </DropdownItem>
        </DropdownMenu>
      </Dropdown>
    );
  }
}

export default SelectDApp;
